import React, { useEffect, useRef, useState } from 'react'
import { useSelector } from 'react-redux'
import { Link } from 'react-scroll'
import DarkModeSwitch from '../features/DarkModeSwitch'
import { useScrollPosition } from '../features/useScrollPosition'
export function AppHeader() {
  const isDark = useSelector((state) => state.featureModule.isDark)
  const containerRef = useRef()
  const [hideOnScroll, setHideOnScroll] = useState('')

  useScrollPosition(({ prevPos, currPos }) => {
    const isShow = currPos.y > prevPos.y
    if (isShow) setHideOnScroll('hide')
    else setHideOnScroll('')
  }, [hideOnScroll])

  useEffect(() => {
    if (isDark) containerRef.current.classList.add('night')
    else containerRef.current.classList.remove('night')
  })

  return (
    <header
      ref={containerRef}
      className={`flex column space-around align-center header-container ${hideOnScroll}`}>
      <section className="flex align-center header-logo">
        <span>
          <span>{'<'}</span>
          <span className="purple">{'n'}</span>
          <span>{'Dev /> '}</span>
        </span>
        <DarkModeSwitch />
      </section>
      <nav className="flex">
        <Link
          to="projects"
          spy={true}
          smooth={true}
          offset={-170}
          duration={100}
          className="nav-link"
        >
          Projects
        </Link>
        <Link
          to="about"
          spy={true}
          smooth={true}
          offset={-170}
          duration={100}
          className="nav-link"
        >
          About Me
        </Link>
      </nav>
      <section className="dark-mode-switch"></section>
    </header>
  )
}
