import React, { useEffect } from 'react'
import Particles from 'react-tsparticles'
import { loadFull } from 'tsparticles'
import { useCallback } from 'react'
import { useSelector } from 'react-redux'

export function AppParticles() {
  const isDark = useSelector((state) => state.featureModule.isDark)

  const dayOptions = {
    background: {
      color: {
        value: '#fefefe',
      },
    },
    fpsLimit: 120,
    interactivity: {
      events: {
        onClick: {
          enable: true,
          mode: 'push',
        },
        onHover: {
          enable: true,
          mode: 'bubble'
        },
        resize: true,
      },
      modes: {
        push: {
          quantity: 1,
        },
        bubble: {
          size: 15,
          distance: 100,
          color: '#9631b5',
        },
        repulse: { distance: 100, duration:1},
        grab: {distance: 200, duration: 0.4}
      },
    },
    particles: {
      color: {
        value: '#000',
      },
      links: {
        color:  '#000',
        distance: 150,
        enable: true,
        opacity: 0.4,
        width: 0.5,
      },
      move: {
        directions: 'none',
        enable: true,
        outModes: {
          default: 'bounce',
        },
        random: true,
        speed: 0.5,
        straight: false,
      },
      number: {
        density: {
          enable: true,
          area: 800,
        },
        value: 40,
      },
      opacity: {
        value: 0.25,
      },
      shape: {
        type: 'triangle',
      },
      size: {
        value: { min: 4, max: 9},
      },
    },
    detectRetina: true,
  }

  const darkOptions = {
    background: {
      color: {
        value: '#1e1919e2',
      },
    },
    fpsLimit: 120,
    interactivity: {
      events: {
        onClick: {
          enable: true,
          mode: 'push',
        },
        onHover: {
          enable: true,
          mode: 'bubble',
        },
        resize: true,
      },
      modes: {
        push: {
          quantity: 1,
        },
        bubble: {
          size: 15,
          distance: 100,
          color: '#9631b5',
        },
      },
    },
    particles: {
      color: {
        value: '#fff',
      },
      links: {
        color: '#fff',
        distance: 150,
        enable: false,
        opacity: 0.5,
        width: 1,
      },
      move: {
        directions: 'none',
        enable: true,
        outModes: {
          default: 'bounce',
        },
        random: false,
        speed: 0.3,
        straight: false,
      },
      number: {
        density: {
          enable: true,
          area: 800,
        },
        value: 150,
      },
      opacity: {
        value: 0.8,
        anim: {
          enable: true,
          speed: 1.2181158184520178,
          opacity_min: 0,
          sync: false
        }
      },
      shape: {
        type: 'edge',
      },
      size: {
        value: { min: 1, max: 3 },
      },
    },
    detectRetina: true,
  }
  const particlesInit = useCallback(async (engine) => {
    await loadFull(engine)
  }, [])

  return isDark ? (
    <Particles id="tsparticles" init={particlesInit} options={darkOptions} />
  ) : (
    <Particles id="tsparticles" init={particlesInit} options={dayOptions} />
  )
}
